import pauseIcon from '../assets/svg/pause-fill.svg';
import playIcon from '../assets/svg/play-fill.svg';
import { createGallery } from './galleryModal';
import { getTranslate, getTranslateBirdData } from './ru-en';
import enTranslate from "./enTranslate";
import ruTranslate from "./ruTranslate";
import birdsDataEn from './birdsEN';
import birdsData from './birds.js';

const playerButton = document.querySelector('.player-button');
const audio = document.querySelector('.songbird__random-audio-header');
const audioTwo = document.querySelector('.bird-audio');
const progressBarTwo = document.querySelector('.bar-two');
const progressBarOne = document.querySelector('.bar-one');
const fillProgressOne = document.querySelector('.fill-one');
const fillProgressTwo = document.querySelector('.fill-two');
const headerIcon = document.querySelector('.header-icon');
const cardIcon = document.querySelector('.card-icon');
const durationTime = document.querySelectorAll('.duration');
const correctTime = document.querySelectorAll('.correct-time');
const playerButtonTwo = document.querySelector('.button-two');


let songPlayHeader = false;
let songPlayCard = false;


createGallery(birdsData);

export let translate = 'ru'; 
const ruButton = document.querySelector('.ru');
const enButton = document.querySelector('.en');


function setLocalStorage() {
  localStorage.setItem('translate', translate);
}

function getLocalStorage() {
  if(localStorage.getItem('translate')) {
    translate = localStorage.getItem('translate');
    if(translate === 'ru') {
      getTranslate(ruTranslate);
      getTranslateBirdData(birdsData);
    } else {
      getTranslate(enTranslate);
      getTranslateBirdData(birdsDataEn);
    };
  }
}


ruButton.addEventListener('click', e=>{
  translate = 'ru';
  localStorage.setItem('translate', translate);
  getTranslate(ruTranslate);
  getTranslateBirdData(birdsData);
})
enButton.addEventListener('click', e=>{
  translate = 'en';
  localStorage.setItem('translate', translate);
  getTranslate(enTranslate);
  getTranslateBirdData(birdsDataEn);
})



getLocalStorage()
window.addEventListener('beforeunload', setLocalStorage);
// window.addEventListener('load', getLocalStorage);



function playAudio(songPath, pathIcon) {
  songPath.play();
  pathIcon.src = pauseIcon;
}

function pauseAudio(songPath, pathIcon) {
  songPath.pause();
  pathIcon.src = playIcon;
}

playerButtonTwo.addEventListener('click', ()=> {
  if(songPlayCard === false) {
    songPlayCard = true;
    playAudio(audioTwo,cardIcon);
    

  } else {

    songPlayCard = false;
    pauseAudio(audioTwo,cardIcon);
    
  }
  
})
playerButton.addEventListener('click', ()=> {
  if(songPlayHeader === false) {
    
    songPlayHeader = true;
    playAudio(audio,headerIcon);

  } else {
    songPlayHeader = false;
    pauseAudio(audio,headerIcon);
    
  }
  
})

 function progressUpdate(e, fill, dur, corr) {
  // const {duration, currentTime} = e.srcElement;
  // const duration = e.duration;
  const duration = e.duration;
  const currentTime = e.currentTime
  const progressFill = (currentTime / duration)* 100;
  fill.style.width = `${progressFill}%`;




  // setTimeAudio(duration, currentTime, dur, corr)

}

function setProgress(e) {
  const barWidth = this.clientWidth;
  const clickX = e.offsetX;
  const duration = audio.duration;

  audio.currentTime = (clickX / barWidth) * duration;

}
function setProgressTwo(e) {
  const barWidth = this.clientWidth;
  const clickX = e.offsetX;
  const duration = audioTwo.duration;

  audioTwo.currentTime = (clickX / barWidth) * duration;

}

audio.addEventListener('timeupdate', (e) => {
  progressUpdate(audio, fillProgressOne, durationTime[0], correctTime[0])
});

audio.addEventListener('ended', () => {
  songPlay = false;
  headerIcon.src = playIcon;
});
progressBarOne.addEventListener('click', setProgress);



audioTwo.addEventListener('timeupdate', (e)=> {
  progressUpdate(audioTwo, fillProgressTwo, durationTime[1], correctTime[1])
});

audioTwo.addEventListener('ended', () => {
  songPlay = false;
  cardIcon.src = playIcon;
});
progressBarTwo.addEventListener('click', setProgressTwo);



const muteBar = document.querySelectorAll('.mute-fill');
const muteProgress = document.querySelectorAll('.mute-progress');



muteProgress[0].addEventListener('click',  e => {
  const sliderWidth = muteProgress[0].clientWidth;
  const newVolume = e.offsetX / parseInt(sliderWidth);
  audio.volume = newVolume;
  muteBar[0].style.width = `${newVolume * 100}%`;

});

muteProgress[1].addEventListener('click',  e => {
  const sliderWidth = muteProgress[1].clientWidth;
  const newVolume = e.offsetX / parseInt(sliderWidth);
  audioTwo.volume = newVolume;
  muteBar[1].style.width = `${newVolume * 100}%`;

});

const birdsListContainer = document.querySelector('.songbird__list');
const nextButton = document.querySelector('.songbird__button-next');
const birdsList =  document.querySelectorAll('.birds-list');

birdsListContainer.addEventListener('click', e => {
  // songPlayHeader = false;
  songPlayCard = false;
  // headerIcon.src = playIcon;
  cardIcon.src = playIcon;
})
// birdsList.forEach()
nextButton.addEventListener('click', e => {
  songPlayHeader = false;
  songPlayCard = false;
  headerIcon.src = playIcon;
  cardIcon.src = playIcon;
})




const birdsCardsButton = document.querySelectorAll('.birds-card-button');
const birdsCardsIcon = document.querySelectorAll('.birds-cards-icon');
const birdsCardsAudio = document.querySelectorAll('.birds-audio-cards');
const birdsCardsBar = document.querySelectorAll('.birds-cards-bar');
const birdsCardsFillBar = document.querySelectorAll('.birds-card-fill');
const birdsCardsMuteBar = document.querySelectorAll('.birds-cards-mute');
const birdsCardsMuteBarFill = document.querySelectorAll('.birds-card-mute-fill');
const birdsCardsCorrectTime = document.querySelectorAll('.correct-time-cards');
const birdsCardsDurationCards= document.querySelectorAll('.duration-cards');


birdsCardsButton.forEach((item, index)=> {
  let play = false;
  item.addEventListener('click', e=> {
    
    if(play === false) {
    
      play = true;
      playAudio(birdsCardsAudio[index],birdsCardsIcon[index]);
  
    } else {
      play = false;
      pauseAudio(birdsCardsAudio[index],birdsCardsIcon[index]);
      
    }
  })
})

birdsCardsMuteBar.forEach((item, index) => {
  item.addEventListener('click', e=> {
    const sliderWidth = birdsCardsMuteBar[index].clientWidth;
    const newVolume = e.offsetX / parseInt(sliderWidth);
    birdsCardsAudio[index].volume = newVolume;
    birdsCardsMuteBarFill[index].style.width = `${newVolume * 100}%`;
  })
})

birdsCardsAudio.forEach((item, index)=> {
  item.addEventListener('timeupdate', e=> {
    progressUpdate(birdsCardsAudio[index], birdsCardsFillBar[index], birdsCardsDurationCards[index], birdsCardsCorrectTime[index])
  })
})
birdsCardsBar.forEach((item, index)=> {
  item.addEventListener('click', e => {
    setTest(birdsCardsBar[index], birdsCardsAudio, index, e)
  })
})
function setTest(e, audio, index, i) {
  const barWidth = e.clientWidth;
  const clickX = i.offsetX;
  const duration = audio[index].duration;

  audio[index].currentTime = (clickX / barWidth) * duration;

}



const allAudio = document.querySelectorAll('.birds-audio');



function getTime() {
  allAudio.forEach((item, index) =>{
    let duration = item.duration;
    let currentTime = item.currentTime;
    let durSec = parseInt(duration % 60);
    let durMin = parseInt((duration/60) % 60);
    let corrSec = parseInt(currentTime % 60);
    let corrMin = parseInt((currentTime/60) % 60);


    durationTime[index].textContent = `${durMin.toString().padStart(2,0)}:${durSec.toString().padStart(2,0)}`;
  
    
    correctTime[index].textContent = `${corrMin.toString().padStart(2,0)}:${corrSec.toString().padStart(2,0)}`;
  
  })
}

function getTimeCards() {
  birdsCardsAudio.forEach((item, index) =>{
    let duration = item.duration;
    let currentTime = item.currentTime;
    let durSec = parseInt(duration % 60);
    let durMin = parseInt((duration/60) % 60);
    let corrSec = parseInt(currentTime % 60);
    let corrMin = parseInt((currentTime/60) % 60);


    birdsCardsDurationCards[index].textContent = `${durMin.toString().padStart(2,0)}:${durSec.toString().padStart(2,0)}`;
  
    
    birdsCardsCorrectTime[index].textContent = `${corrMin.toString().padStart(2,0)}:${corrSec.toString().padStart(2,0)}`;
  
  })
}

setInterval(getTime, 500);
setInterval(getTimeCards, 500);