import enTranslate from "./enTranslate";
import ruTranslate from "./ruTranslate";
import birdsData from './birds.js';


export function getTranslate(obj) {
  document.querySelector('.start__pages-title').textContent = obj.titleNameStart;
  document.querySelector('.button__start').textContent = obj.startPageButton;
  document.querySelector('.start__page').textContent =obj.headerStart;
  document.querySelector('.gallery__modal').textContent = obj.headerGallery;
  document.querySelectorAll('.songbird__items-list').forEach( (item, index)=>{
    item.textContent = obj.gameList[index];
  })
  document.querySelector('.song__bird-none').textContent = obj.takeBird;
  document.querySelector('.songbird__button-next').textContent = obj.nextButton;
  document.querySelector('.score__modal__button').textContent = obj.buttonAgain;
  document.querySelector('.modal-title').textContent = obj.galleryModal;
  document.querySelector('.button-modal').textContent = obj.modalButton;
  document.querySelector('.score__modal-next').textContent = obj.nextGame;
}

export function getTranslateBirdData(data) {
  const name = document.querySelectorAll('.cards-bird-name');
  const description = document.querySelectorAll('.card-description');
  let count = 0;
  for(let j = 0; j < data.length; j++) {
    for(let i = 0; i < data[j].length; i++) {
      name[count].textContent = data[j][i].name;
      description[count].textContent = data[j][i].description;
      // console.log(name[count].textContent, data[j][i].name)
      count++;
    }
  }

}

