import birdsData from "./birds";

const modalGallery = document.querySelector('.modal-body');

export function createGallery(obj) {
  modalGallery.innerHTML = '';
    for(let j = 0; j < obj.length; j++) {
      for(let i = 0; i < obj[j].length; i++) {
        let text = `<div class="modal__item-card"> <div class="card p-3 songbird__card-container">
        <img src="${obj[j][i].image}" class="card-img-top songbird__card-img bird-img" alt="bird" loading="lazy">
        <ul class="list-group list-group-flush">
          <li class="list-group-item  bird-name cards-bird-name">${obj[j][i].name}</li>
          <li class="list-group-item  bird-species">${obj[j][i].species}</li>
          <li class="list-group-item songbird__audio-container">
          <div class="player-container">
          <div class="player-button birds-card-button">
            <img src="assets/source/play-fill.svg" alt="" class="player-icon header-icon birds-cards-icon">
          </div>
          <audio preload="metadata" class="songbird__random-audio bird-audio birds-audio-cards" src="${obj[j][i].audio}"></audio>
          <div class="player-progress">
            <div class="progress-bar bar-one birds-cards-bar">
              <div class="fill-one birds-card-fill" style="width: 0%;"></div>
            </div>
            <div class="progress-time">
              <div class="correct-time-cards">00:00</div>
              <div class="duration-cards">00:00</div>
            </div>
          </div>
          <div class="mute-container">
            <img src="assets/source/volume-down-fill.svg" alt="" class="mute-icon">
            <div class="mute-progress birds-cards-mute">
              <div class="mute-fill birds-card-mute-fill" style="width: 100%;"></div>
            </div>
          </div>
        </div>   
          </li>
          <div class="card-body">
            <p class="card-text card-text bird-description card-description">${obj[j][i].description}</p>
          </div>
        </ul></div></div>`;


        
    modalGallery.insertAdjacentHTML('beforeend', text);
    }
  }
}
